import {Component, Input, OnInit} from '@angular/core';
import {RequestStatus} from '@app/modules/newscenter/RequestStatus.model';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {BehaviorSubject} from 'rxjs';

import {ProductsService} from '../../../../../services';

@Component({
    selector: 'sb-delete-product-modal',
    templateUrl: './delete-product-modal.component.html',
    styleUrls: ['./delete-product-modal.component.scss']
})
export class DeleteProductModalComponent implements OnInit {
    @Input() productId!: number
    status: BehaviorSubject<RequestStatus> = new BehaviorSubject<RequestStatus>({status: 'idle'})

    constructor(public productsService: ProductsService, public ngbActiveModal: NgbActiveModal) {
    }

    ngOnInit(): void {
    }

    deleteProduct() {
        this.status.next({status: 'pending'})
        this.productsService.deleteProduct(this.productId).subscribe(
            () => {
                this.status.next({status: 'success'})
                setTimeout(() => {
                    this.ngbActiveModal.close(true)
                }, 300)
            }
        )
    }

}
