import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {RequestStatus} from '@app/modules/newscenter/RequestStatus.model';
import {PictureModel} from '@myclapp/myclapp-server';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {BehaviorSubject} from 'rxjs';
import {debounceTime, map} from 'rxjs/operators';

import {ProductsService} from '../../../../../services';

@Component({
    selector: 'app-add-product-modal',
    templateUrl: './add-product-modal.component.html',
    styleUrls: ['./add-product-modal.component.scss'],
})
export class AddProductModalComponent implements OnInit {


    public model: any;
    public selectedCustom: boolean = false;
    previewURL: any;
    submit$: BehaviorSubject<RequestStatus> = new BehaviorSubject<RequestStatus>({status: 'idle'})

    productForm!: FormGroup;

    pictureOptions: PictureModel[] = []
    loadingPictureOptions$: BehaviorSubject<RequestStatus> = new BehaviorSubject<RequestStatus>({status: 'idle'})


    constructor(public productsService: ProductsService, private fb: FormBuilder, private ngbActiveModal: NgbActiveModal) {
    }

    pictureSelected(item: any) {
        if (item.item.id === 'custom') {
            this.selectedCustom = true;
            this.previewURL = null;
        } else {
            this.selectedCustom = false;
            this.previewURL = item.item.pic;
        }

    }

    ngOnInit(): void {
        this.productForm = this.fb.group({
                name: ['', Validators.required],
                description: ['', Validators.required],
                price: ['', Validators.required],
                picture: ['', Validators.required],
                category: ['', Validators.required]
            },
        );
        this.controls.name.valueChanges.pipe(debounceTime(500), map(term => {
            if (term !== '' && typeof term === 'string') {
                if (term.length >= 3) {
                    this.loadingPictureOptions$.next({status: 'pending'})
                    this.productsService.getPictureForTerm(term).subscribe(model => {
                        this.pictureOptions = model.items
                        this.loadingPictureOptions$.next({status: 'success'})
                    })
                }
            }
        })).subscribe()

        this.controls.picture.valueChanges.pipe(map(id => {
            if (id === 'custom') {
                this.selectedCustom = true;
                this.previewURL = null
            } else {
                this.selectedCustom = false;
                for (const picture of this.pictureOptions) {
                    if (picture.id === id) {
                        this.previewURL = 'data:image/gif;base64,' + picture.data
                    }
                }

            }
        })).subscribe()
    }

    addProduct(): void {
        this.submit$.next({status: 'pending'})
        this.productsService.addProduct(this.productForm.value).subscribe(() => {
            this.submit$.next({status: 'success'})
            setTimeout(() => {
                this.ngbActiveModal.close(true)
            }, 300)
        })
    }

    public addPicture(event: any) {
        if (event.target.files && event.target.files[0]) {
            const reader = new FileReader();
            reader.onload = (onLoadEvent: any) => {
                this.previewURL = onLoadEvent.target.result;
            };
            reader.readAsDataURL(event.target.files[0]);
        }
    }

    get controls() {
        return this.productForm.controls
    }
}
