import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {RequestStatus} from '@app/modules/newscenter/RequestStatus.model';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {Observable, of} from 'rxjs';

@Component({
    selector: 'sb-standard-modal',
    templateUrl: './standard-modal.component.html',
    styleUrls: ['./standard-modal.component.scss']
})
export class StandardModalComponent implements OnInit {
    @Input() title!: string;
    @Output() closeFunction: EventEmitter<any> = new EventEmitter<any>()
    @Input() closeText: string = 'Schließen';
    @Input() buttonDisabled: boolean = false;
    @Input() type: string = 'primary';
    @Input() buttonLoadingIndicator: Observable<RequestStatus> = of({status: 'idle'});
    @Input() dismissOnClose: boolean = true;

    // @Input() form!: FormGroup

    constructor(public ngbActiveModal: NgbActiveModal) {
    }

    ngOnInit(): void {
    }

    close() {
        this.closeFunction.emit()
        if (this.dismissOnClose) this.ngbActiveModal.close(true)
    }
}
