import {ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnInit, QueryList, ViewChildren} from '@angular/core';
import {DeleteProductModalComponent} from '@app/modules/products/components/delete-product-modal/delete-product-modal.component';
import {EditProductModalComponent} from '@app/modules/products/components/edit-product-modal/edit-product-modal.component';
import {SBSortableHeaderDirective, SortEvent} from '@app/modules/products/directives';
import {ProductModel} from '@myclapp/myclapp-server';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {Observable} from 'rxjs';

import {ProductsService} from '../../../../../services';

@Component({
    selector: 'sb-ng-bootstrap-table',
    changeDetection: ChangeDetectionStrategy.OnPush,
    templateUrl: './ng-bootstrap-table.component.html',
    styleUrls: ['ng-bootstrap-table.component.scss'],
})
export class NgBootstrapTableComponent implements OnInit {
    @Input() pageSize = 4;

    products$!: Observable<ProductModel[]>;
    total$!: Observable<number>;
    sortedColumn!: string;
    sortedDirection!: string;

    @ViewChildren(SBSortableHeaderDirective) headers!: QueryList<SBSortableHeaderDirective>;

    constructor(
        public productsService: ProductsService,
        private changeDetectorRef: ChangeDetectorRef,
        private modalService: NgbModal
    ) {
    }

    ngOnInit() {
        this.productsService.pageSize = this.pageSize;
        this.products$ = this.productsService.results$
        this.total$ = this.productsService.total$;
        this.productsService.refreshRequired$.subscribe(bool => {
            if (bool) {
                this.refreshTable()
                this.productsService.refreshRequired$.next(false)
            }
        })
    }

    onSort({column, direction}: SortEvent) {
        this.sortedColumn = column;
        this.sortedDirection = direction;
        this.productsService.sortColumn = column as keyof ProductModel;
        this.productsService.sortDirection = direction;
        this.changeDetectorRef.detectChanges();
    }

    editProduct(id: number) {
        const modalRef = this.modalService.open(EditProductModalComponent, {
            ariaLabelledBy: 'Produkt bearbeiten',
            size: 'lg',
            centered: true
        })
        modalRef.componentInstance.productId = id;
    }

    deleteProduct(id: number) {
        const modalRef = this.modalService.open(DeleteProductModalComponent, {
            ariaLabelledBy: 'Produkt löschen',
            size: 'lg',
            centered: true,
        });
        modalRef.componentInstance.productId = id;
    }

    refreshTable() {
        this.productsService.pageSize = this.pageSize;
        this.products$ = this.productsService.results$;
        this.total$ = this.productsService.total$;
    }


}
